import React, { Component } from "react";
import Slider from "react-rangeslider";
import "react-rangeslider/lib/index.css";
import { Container, Flex, Box } from 'theme-ui'
import Reveal from '@solid-ui-components/Reveal'
import {Link} from 'gatsby';
import './react-slider.css'


class PriceBlock02 extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
          value: 1000,
          planName: "1 - 1000",
          price: "$0.00",
          active1: "active",
          active2: "",
          active3: "",
          active4: "",
          active5: "",
          active6: "",
          active7: "",
          active8:"",
          link: "https://one.mumara.com/signup?pid=57"
        };
    }
    
    handleChangeStart = () => {
        console.log("Change event started");
    };
    
    handleChange = (value) => {
        this.setState({
          value: value
        });
        if(this.state.value < 1001) {
          this.setState({
            price: "$0.00",
            planName: "1 - 1,000",
            active1: "active", active2: "", active3: "", active4: "", active5: "", active6: "", active7: "", active8: ""
          });
        } else if(this.state.value > 1000 && this.state.value < 5001) {
          this.setState({
            price: "17.00",
            planName: "1,001 - 5,000",
            active1: "", active2: "active", active3: "", active4: "", active5: "", active6: "", active7: "", active8: ""
          })
        } else if(this.state.value > 5000 && this.state.value < 25001) {
          this.setState({
            price: "$67.00",
            planName: "5,001 - 25,000",
            active1: "", active2: "", active3: "active", active4: "", active5: "", active6: "", active7: "", active8: ""
          })
        } else if(this.state.value > 25000 && this.state.value < 50001) {
          this.setState({
            price: "$117.00",
            planName: "25,001 - 50,000",
            active1: "", active2: "", active3: "", active4: "active", active5: "", active6: "", active7: "", active8: ""
          })
        } else if(this.state.value > 50000 && this.state.value < 100001) {
          this.setState({
            price: "$197.00",
            planName: "50,001 - 100,000",
            active1: "", active2: "", active3: "", active4: "", active5: "active", active6: "", active7: "", active8: ""
          })
        } else if(this.state.value > 100000 && this.state.value < 250001) {
          this.setState({
            price: "$397.00",
            planName: "100,001 - 250,000",
            active1: "", active2: "", active3: "", active4: "", active5: "", active6: "active", active7: "", active8: ""
          })
        } else if(this.state.value > 250000 && this.state.value < 1000001) {
          this.setState({
            price: "$797.00",
            planName: "250,001 - 1,000,000",
            active1: "", active2: "", active3: "", active4: "", active5: "", active6: "", active7: "active", active8: ""
          })
        } else {
          this.setState({
            price: "Custom",
            planName: "1,000,000+",
            active1: "", active2: "", active3: "", active4: "", active5: "", active6: "", active7: "", active8: "active"
          })
        }
    };
    
    handleChangeComplete = () => {
        console.log("Change event completed");
    };
    
    changeInput = (e) => {
        var val = e.target.value;
        this.setState({
          value: val
        });
        if(val < 1001) {
          this.setState({
            price: "$0.00",
            planName: "1 - 1,000",
            active1: "active", active2: "", active3: "", active4: "", active5: "", active6: "", active7: "", active8: ""
          });
        } else if(val > 1000 && val < 5001) {
          this.setState({
            price: "$17.00",
            planName: "1001 - 5,000",
            active1: "", active2: "active", active3: "", active4: "", active5: "", active6: "", active7: "", active8: ""
          })
        } else if(val > 5000 && val < 25001) {
          this.setState({
            price: "$67.00",
            planName: "5,001 - 25,000",
            active1: "", active2: "", active3: "active", active4: "", active5: "", active6: "", active7: "", active8: ""
          })
        } else if(val > 25000 && val < 50001) {
          this.setState({
            price: "$117.00",
            planName: "25,001 - 50,000",
            active1: "", active2: "", active3: "", active4: "active", active5: "", active6: "", active7: "", active8: ""
          })
        } else if(val > 50000 && val < 100001) {
          this.setState({
            price: "$197.00",
            planName: "50,001 - 100,000",
            active1: "", active2: "", active3: "", active4: "", active5: "active", active6: "", active7: "", active8: ""
          })
        } else if(val > 100000 && val < 250001) {
          this.setState({
            price: "$397.00",
            planName: "100,001 - 250,000",
            active1: "", active2: "", active3: "", active4: "", active5: "", active6: "active", active7: "", active8: ""
          })
        } else if(val > 250000 && val < 1000001) {
          this.setState({
            price: "$797.00",
            planName: "250,001 - 1,000,000",
            active1: "", active2: "", active3: "", active4: "", active5: "", active6: "", active7: "active", active8: ""
          })
        } else {
          this.setState({
            price: "Custom",
            planName: "1,000,000+",
            active1: "", active2: "", active3: "", active4: "", active5: "", active6: "", active7: "", active8: "active"
          })
        }
    };
    
    render() {
        const { value } = this.state;
        return (
          <Container as={Reveal}>
            <Box className="row slider">

              <Box className="slider-left-block">
                <Box className="slider-input-blk">
                  <h3>Your contact list size?</h3>
                  <p>Select the plan below as per your contact list size</p>
                  <input className="subscribers-input" type="number" min="1000" max="1000001" value={value} onChange={this.changeInput} />
                </Box>
                <Slider
                  min={1000}
                  max={1000001}
                  value={value}
                  tooltip={false}
                  onChangeStart={this.handleChangeStart}
                  onChange={this.handleChange}
                  onChangeComplete={this.handleChangeComplete}
                />
                {/* <Box className="slider-value">{this.state.planName}</Box> */}
                <Box className="slider-desc-val">Plan Price</Box>
                <Box className="slider-value">{this.state.price}</Box>
                <Box className="ohbblk">
                  <Link className="btn btn-ohb" to={this.state.link}>Order Now</Link>
                </Box>
              </Box>

              <Box className="slider-right-block">
                <Box className="slider-packages-header">
                  <h3>Choose Your Plan!</h3>
                  <p>Test the platform with no commitment</p>
                </Box>
                <Box className="slider-packages">

                  <Box className="one-hpkg-top">
                    <Box>Package</Box>
                    <Box>Package Price</Box>
                  </Box>

                  <Box className={`one-hpkg `+this.state.active1}>
                    <Box>1 - 1000</Box>
                    <Box>Free</Box>
                  </Box>

                  <Box className={`one-hpkg `+this.state.active2}>
                    <Box>1001 - 5000</Box>
                    <Box>$17.00</Box>
                  </Box>
                  
                  <Box className={`one-hpkg `+this.state.active3}>
                    <Box>5001 - 25000</Box>
                    <Box>$67.00</Box>
                  </Box>

                  <Box className={`one-hpkg `+this.state.active4}>
                    <Box>25001 - 50000</Box>
                    <Box>$117.00</Box>
                  </Box>

                  <Box className={`one-hpkg `+this.state.active5}>
                    <Box>50001 - 100000</Box>
                    <Box>$197.00</Box>
                  </Box>

                  <Box className={`one-hpkg `+this.state.active6}>
                    <Box>100001 - 250000</Box>
                    <Box>$397.00</Box>
                  </Box>

                  <Box className={`one-hpkg `+this.state.active7}>
                    <Box>250001 - 1000000</Box>
                    <Box>$797.00</Box>
                  </Box>

                  <Box className={`one-hpkg `+this.state.active8}>
                    <Box>1000000+</Box>
                    <Box>Custom</Box>
                  </Box>

                </Box>
              </Box>
              
            </Box>
          </Container>
        );
    }
}


export default PriceBlock02;